.contactList {
  width: 480px;
}

.contactList_item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 18px;
}
.box_title {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 20px auto;
  width: 550px;
  padding: 30px;
  box-shadow: 10px 5px 10px 5px rgba(20, 1, 1, 0.89);
}
.button {
  background: rgba(138, 105, 105, 0.2);
  box-shadow: 10px 5px 10px 5px rgba(20, 1, 1, 0.89);

  color: rgb(14, 0, 0);
  padding: 0.25em;
  text-decoration: none;

  cursor: pointer;
  font-size: 9px;
  font-weight: 700;
  line-height: 2.81;
  margin: 5px 0 1em;
  max-width: 160px;
  text-decoration: none;
  text-transform: uppercase;
  width: 100%;

  border: 1px solid;
  border-radius: 5px;
}

.button:hover {
  border: px solid;
  border-radius: 5px;
  box-shadow: inset 0 0 20px rgba(223, 192, 221, 0.5),
    0 0 20px rgba(128, 102, 102, 0.2);
  outline-color: rgba(56, 53, 53, 0);
  /* outline-offset: 15px; */
}
